import React, { Component } from "react";
import MultiThemeProvider from "material-ui/styles/MuiThemeProvider";
import MyDrawer from "./MyDrawer";

import {
  Card,
  CardActions,
  CardHeader,
  CardMedia,
  CardTitle,
  CardText
} from "material-ui/Card";
import FlatButton from "material-ui/FlatButton";
import "./main.css";
// import "./home.css";
// import "./flex.css";
// import { colors } from "material-ui/styles";

class Layout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keyword: "tech",
      result: null,
      loaded: false,
      filtered: []
    };
  }

  render() {
    const { loaded } = this.state;

    return (
      <div className="container">
      <MultiThemeProvider>
                <MyDrawer title="News API"/>
                </MultiThemeProvider>
<br/>
        {this.search()}
        <br/>
        {loaded ? this.MyCard() : <h1 className="display-4">Loading...</h1>}
      </div>
    );
  }

  search() {
    return (
      <div className="text">
        <input
          autoFocus
          onChange={this.handleText}
          className="form-control mr-sm-2 text"
          type="search"
          placeholder="Search News"
          aria-label="Search"
        />
      </div>
    );
  }

  handleText = e => {
    // console.log(e.target.value);
    let new_keyword = e.target.value;
    let keyword = new_keyword.toLowerCase();
    console.log(keyword);

    let filtered = this.state.result.filter(item => {
      return item.title.toLowerCase().indexOf(keyword) > -1;
    });

    // console.log(filtered);

    this.setState({
      filtered
    });
  };

  componentDidMount() {
    const { keyword } = this.state;
    let url =
      "https://newsapi.org/v2/everything?q=" +
      keyword +
      "&from=2018-09-01&sortBy=popularity&apiKey=aad1919ff1fc4ce1bbc390acabd07b88";
    console.log(url);

    fetch(url)
      .then(res => res.json())
      .then(val => {
        const result = val.articles;
        // console.log(result);
        this.setState({
          result: result,
          loaded: true
        });
      });
  }

  // getDrops() {
  //   const { options } = this.state;
  //   // console.log(optiosn);

  // }

  renderNews() {
    const { result, filtered } = this.state;
    const data = filtered.length === 0 ? result : filtered;
    return (
      <div className="mix">
        {data.map((obj, i) => {
          return (
            <div className="mix2" key={i}>
              <a href={obj.url} target="_blank">
                <div className="card card--article not-visible">
                  <div className="card-body">
                    <div className="card-circle card-circle--article">
                      <img
                        className="card-circle card-circle--article"
                        src={obj.urlToImage}
                        alt="d"
                      />
                    </div>
                    <div className="card-title">{obj.title}</div>
                    <div className="card-description card-description--clamp-3">
                      {obj.description}
                    </div>
                  </div>
                  <div className="card-hero">
                    <div className="card-image">
                      <img
                        className="card-image"
                        src={obj.urlToImage}
                        alt="d"
                      />
                    </div>
                  </div>
                  <div className="card-footer">
                    <div
                      className="card-footer-wrapper"
                      layout="row bottom-left"
                    >
                      <div className="card-type is-notShownIfHover">
                        {obj.source.name}
                      </div>
                      <div className="card-tag is-notShownIfHover">
                        {obj.publishedAt}
                      </div>
                      <div className="card-tag is-shownIfHover">
                        Read Margaret Gould Stewart's article
                      </div>
                      <div
                        className="card-medium is-shownIfHover"
                        self="right"
                      />
                    </div>
                  </div>
                </div>
              </a>
            </div>
          );
        })}
      </div>
    );
  }

  MyCard() {
    const { result, filtered } = this.state;
    const data = filtered.length === 0 ? result : filtered;

    return (
      <div className="mix mt-3">
        {data.map((val, i) => {
          return (
            <MultiThemeProvider className="mt-3" key={i}>
              <Card>
                <CardHeader
                  title={val.author}
                  subtitle={val.source.name}
                  avatar={val.urlToImage}
                />

                <CardMedia
                  overlay={
                    <CardTitle title={val.title} subtitle={val.publishedAt} />
                  }
                >
                  <img src={val.urlToImage} alt={val.title} />
                </CardMedia>
                <CardTitle title={val.title} subtitle={val.source.name} />
                <CardText>{val.description}</CardText>
                <CardActions>
                  <a href={val.url}>
                    <FlatButton backgroundColor="#E0E0E0" label="View Source" />
                  </a>
                </CardActions>
              </Card>
            </MultiThemeProvider>
          );
        })}
      </div>
    );
  }
}

export default Layout;



